<template>
    <yd-layout class="remindToday">
        <yd-navbar bgcolor="#70b6ef" title="" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <!-- @touchmove.native="test($event)" -->
        <ul id="js_listview" class="md-wo-list">
            <li data-id="1" v-for="(item, index) in list" :key="index" :appid="item.appid" :read="item.read" :remind="item.remind" @click="hrefC(item.url, index)">
                <div class="list-img dot">
                    <!-- <yd-badge type="danger">1</yd-badge> -->
                    <img :src="item.image">
                </div>
                <div class="list-mes">
                    <div class="list-title">
                        <span v-text="item.name"> </span>
                        <span class="leftDay" v-text="item.time"></span>
                    </div>
                    <div class="list-other" v-bind:class="{display:bell==true}">
                        <div><span class="desc" v-text="item.content"></span></div>
                        <img src="../../../static/images/remind/rt.png" class="bells">
                    </div>
                </div>
            </li>
        </ul>
    </yd-layout>
</template>
<script>
import $ from '../../jquery-vendor.js';
import { mapGetters } from 'vuex';
import 'mobiscroll';
export default {
    name: 'remindToday',
    data() {
        return {
            bottomPopup: false,
            bell: false,
            index: 0,
            list: [],
            appidGet: '',
            ifRead: 0,
            ifRemind: 0
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    mounted() {
        document.setTitle('');
        this.initList();
    },
    methods: {
        listview() {
            let vm = this;
            $('#js_listview').mobiscroll().listview({
                theme: 'ios',
                lang: 'zh',
                sortable: false,
                iconSlide: true,
                striped: true,
                actionsWidth: 40,
                fillAnimation: false,
                swipe: 'left',
                actions: [{
                    color: '#D3D3D3',
                    text: '',
                    action: function (event, inst) {
                        vm.appidGet = event.target.getAttribute('appid');
                        vm.get(vm.conf.todayRemind + '/openRemind', {
                            'ssotoken': vm.getUserInfo.empcode,
                            'appid': vm.appidGet
                        }, function (res) {});
                        $(event.target).addClass('display');
                    }
                }, {
                    color: '#FF2929',
                    text: '',
                    action: function (event, inst) {
                        vm.appidGet = event.target.getAttribute('appid');
                        vm.get(vm.conf.todayRemind + '/closeRemind', {
                            'ssotoken': vm.getUserInfo.empcode,
                            'appid': vm.appidGet
                        }, function (res) {});
                        $(event.target).removeClass('display');
                    }
                }]
            });
        },
        initList() {
            let vm = this;
            var promise = new Promise(function (resolve, reject) {
                vm.get(vm.conf.todayRemind + '/getRemindList', {
                    'ssotoken': vm.getUserInfo.empcode
                }, function (res) {
                    vm.list = res.jsonDate;
                    resolve(true);
                    return vm.list;
                });
            });
            promise.then(function (data) {
                if (data) {
                    vm.listview();
                }
            });
            promise.then(function (data) {
                if (data) {
                    for (var i = 0; i < vm.list.length; i++) {
                        if (vm.list[i].remind == 1) {
                            $('li.mbsc-lv-item').eq(i).addClass('display');
                        } else {
                            $('li.mbsc-lv-item').eq(i).removeClass('display');
                        }
                        if (vm.list[i].read == 1) {
                            $('.dot').eq(i).addClass('fff');
                        }
                    }
                }
            });
        },
        hrefC(url, index) {
            let vm = this;
            vm.appidGet = $('.mbsc-lv-item').eq(index).attr('appid');
            vm.get(vm.conf.todayRemind + '/read', {
                'ssotoken': vm.getUserInfo.empcode,
                'appid': vm.appidGet
            }, function (res) {})
            location.href = url;
        }
    }
}
</script>
<style scoped>
.bells {
    width: .31rem;
}

.bells,
.leftDay {
    margin-right: .23rem;
}

.leftDay {
    float: right;
    font-size: .22rem;
    color: #999;
    font-weight: normal;
}

.btnPopup {
    line-height: 0.68rem;
    font-size: 0.26rem;
    text-align: center;
}

.liOne {
    font-size: 0.32rem;
    text-align: center;
    color: #f00;
    margin-bottom: 0.2rem;
    border-bottom: 4px solid #ccc;
    padding-bottom: 0.2rem;
}

.liTwo {
    font-size: 0.32rem;
    text-align: center;
}

.display .list-mes img {
    display: none;
}

.dot.fff::after {
    background-color: #fff;
}

.linka {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}
</style>


